@import "https://fonts.googleapis.com/css2?family=Poppins:wght@400;500;600;700&display=swap";
:root, [data-theme="default"] {
  --color-bg: #fff;
  --color-bg-offset: #f7f7f9;
  --color-text: #373a3c;
  --color-text-offset: #818a91;
  --color-border: #eceeef;
  --color-primary: #ff335f;
  --color-primary-offset: #ff1447;
  --color-secondary: #43a9a3;
}

[data-theme="dark"] {
  --color-bg: #0e141b;
  --color-bg-offset: #32323f;
  --color-text: #fff;
  --color-text-offset: #818a91;
  --color-border: #2d2d2d;
  --color-primary: #ff5277;
  --color-primary-offset: #ff2957;
  --color-secondary: #43a9a3;
  --color-black: #000;
}

@keyframes moveInBottom {
  0% {
    opacity: 0;
    transform: translateY(2rem);
  }

  80% {
    transform: translateY(-1rem);
  }

  100% {
    opacity: 1;
    transform: translate(0);
  }
}

@keyframes moveInLeft {
  0% {
    opacity: 0;
    transform: translateX(-10rem);
  }

  80% {
    transform: translateX(1rem);
  }

  100% {
    opacity: 1;
    transform: translate(0);
  }
}

@keyframes moveInRight {
  0% {
    opacity: 0;
    transform: translateX(10rem);
  }

  80% {
    transform: translateX(-1rem);
  }

  100% {
    opacity: 1;
    transform: translate(0);
  }
}

@keyframes down {
  0% {
    transform: translate(0);
  }

  20% {
    transform: translateY(15px);
  }

  40% {
    transform: translate(0);
  }
}

@keyframes up {
  0% {
    transform: translate(0);
  }

  20% {
    transform: translateY(-15px);
  }

  40% {
    transform: translate(0);
  }
}

:root {
  --main-font: "Poppins", sans-serif;
  --font-small: 1.3rem;
  --font-normal: 1.6rem;
  --font-l: 2rem;
  --font-xl: 2.5rem;
  --font-section-title: 3rem;
  --font-menu: 4.5rem;
  --font-xl: 5rem;
  --font-xxl: 6rem;
}

.section__title {
  text-align: center;
  font-size: var(--font-section-title);
  color: var(--color-text);
}

.section__title--icon {
  color: var(--color-primary);
  padding-left: 1.5rem;
}

*, :after, :before {
  box-sizing: inherit;
  margin: 0;
  padding: 0;
}

html {
  scroll-behavior: smooth;
  scroll-padding-top: 70px;
  font-size: 62.5%;
}

@media (width <= 37.5em) {
  html {
    font-size: 50%;
  }
}

@media (width >= 37.51em) and (width <= 56.25em) {
  html {
    font-size: 55%;
  }
}

@media (width >= 56.26em) and (width <= 75em) {
  html {
    font-size: 60%;
  }
}

@media (width >= 112.5em) {
  html {
    font-size: 65%;
  }
}

body {
  background-color: var(--color-bg);
  box-sizing: border-box;
  font-family: var(--main-font);
  transition: background-color .15s;
  position: relative;
  overflow-x: hidden;
}

a {
  text-decoration: none;
}

ul {
  list-style: none;
}

.background {
  background-color: var(--color-bg);
  z-index: -1;
  width: 100vw;
  height: 100%;
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
}

.divider {
  background-color: var(--color-bg-offset);
  width: 100%;
  height: 20rem;
}

.divider__curved--home {
  clip-path: url("#myCurve");
}

.divider__curved--contact {
  clip-path: url("#myCurveContact");
}

.divider__curved--about {
  clip-path: url("#myCurveAbout");
}

.container {
  max-width: 120rem;
  margin: auto;
  padding: 2rem;
}

.active {
  border-bottom: .5rem solid var(--color-primary);
}

.box-shadow {
  box-shadow: 0 1rem 1rem -1rem #0d2b5a14;
}

.wrapper {
  margin: 2rem 0;
}

.wrapper-l {
  margin: 4rem 0;
}

.move-from-left {
  animation: 1s ease-out moveInLeft;
}

.move-from-right {
  animation: 1s ease-out moveInRight;
}

.add-width {
  width: 100vw;
  height: 100%;
}

.add-width-collage {
  width: 100vw;
  height: 10rem;
}

.upper {
  margin-bottom: -.5rem;
}

.scroll {
  border: 1px solid var(--color-text);
  border-radius: 50%;
  width: 2rem;
  height: 2rem;
  animation: 4s infinite down;
  position: relative;
}

.scroll:before {
  content: "";
  border-left: 1px solid var(--color-text);
  border-bottom: 1px solid var(--color-text);
  width: .7rem;
  height: .7rem;
  position: absolute;
  top: 40%;
  left: 50%;
  transform: translate(-50%, -50%)rotate(-45deg);
}

.scroll-up {
  border: 1px solid var(--color-text);
  border-radius: 50%;
  width: 2rem;
  height: 2rem;
  animation: 4s infinite up;
  position: relative;
}

.scroll-up:before {
  content: "";
  border-left: 1px solid var(--color-text);
  border-top: 1px solid var(--color-text);
  width: .7rem;
  height: .7rem;
  position: absolute;
  top: 60%;
  left: 50%;
  transform: translate(-50%, -50%)rotate(45deg);
}

.btn {
  text-transform: uppercase;
  color: var(--color-text);
  border-radius: 10rem;
  padding: 1rem 6rem;
  transition: all .2s;
  display: inline-block;
}

.btn__contact {
